embla-slider {
  position: relative;
}

embla-slider[fade] embla-slide.is-snapped {
  transition: opacity .4s;
}

.embla-navigation {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 5px;
  width: 100%;
  display: flex;
  position: absolute;
  bottom: 0;
}

.embla-navigation.embla-dots embla-thumbnail {
  opacity: .4;
  background-color: currentColor;
  border-radius: 50%;
  width: 6px;
  height: 6px;
  position: relative;
}

.embla-navigation.embla-dots embla-thumbnail svg {
  display: none;
}

.embla-navigation.embla-dots embla-thumbnail svg .progress {
  stroke-dasharray: 100;
  stroke-dashoffset: 100px;
}

.embla-navigation.embla-dots embla-thumbnail.is-selected {
  opacity: 1;
  background-color: #0000;
  width: 9px;
  height: 9px;
}

.embla-navigation.embla-dots embla-thumbnail.is-selected svg {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(-90deg);
}

.embla-navigation.embla-dots embla-thumbnail.is-selected svg .shadow {
  opacity: .4;
}

.embla-navigation.embla-dots embla-thumbnail.is-selected svg .progress {
  stroke-dashoffset: 0;
  transition: stroke-dashoffset var(--autoplay-speed) linear;
}

.embla-navigation.embla-bars embla-thumbnail {
  vertical-align: top;
  width: 120px;
  height: 6px;
  position: relative;
  overflow: hidden;
}

.embla-navigation.embla-bars embla-thumbnail:before {
  content: "";
  opacity: .4;
  background-color: currentColor;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
}

.embla-navigation.embla-bars embla-thumbnail:after {
  content: "";
  opacity: 1;
  background-color: currentColor;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  transform: translateX(-100%);
}

.embla-navigation.embla-bars embla-thumbnail.is-selected:after {
  transition: transform var(--autoplay-speed) linear;
  transform: translateX(0%);
}

embla-slider:not([autoplay]) .embla-navigation.embla-dots embla-thumbnail.is-selected {
  opacity: 1;
  background-color: currentColor;
}

.embla-controls.embla-simple {
  display: flex;
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translateY(50%);
}

.embla-controls.embla-simple embla-next, .embla-controls.embla-simple embla-prev {
  border-radius: 50%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 5px 5px #0000001a;
}

:is(.embla-controls.embla-simple embla-next, .embla-controls.embla-simple embla-prev):hover {
  transform: scale(1.12);
}

:is(.embla-controls.embla-simple embla-next, .embla-controls.embla-simple embla-prev):active {
  transform: scale(1);
}

.embla-progress {
  width: 100%;
  height: 6px;
  position: relative;
}

.embla-progress:before {
  content: "";
  opacity: .4;
  background-color: currentColor;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.embla-progress:after {
  content: "";
  opacity: 1;
  width: var(--progress);
  background-color: gray;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}
